    .container-table {
        z-index: 2;
        position: relative;
        margin: 30px auto 0;
        padding: 20px;
        background: rgb(255, 255, 255, 0.7);
        border-radius: 20px;
    }
    
    .cityName {
        padding: 20px 0;
        font-size: 1.5em;
        position: relative;
        border: 1px solid;
        border-radius: 20px 20px 0 0;
        width: 100%;
    }
    
    .container-toggle-weat {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 27px;
    }
    
    @media (max-width: 1080px) {
        .cityName {
            text-align: left;
        }
        .city {
            margin-left: 10px;
        }
    }